import { tools } from "@game.object/ts-game-toolbox/index";
import { RgbColor } from "@game.object/ts-game-toolbox/src/data/RgbColor";
import { ChainProperty } from "@game.object/ts-game-toolbox/src/signals/ChainProperty";
import { ViewCollection } from "../ViewCollection";
import { Rect, RectI } from "@game.object/ts-game-toolbox/src/geometries/Rect";
import { CanvasView } from "@game.object/ts-game-toolbox/src/abstract/mvc/views/CanvasView";
import { CameraModel } from "../../models/CameraModel";
import { MenuView } from "./MenuView";
import { RemoteChainProperty } from "../../../tools/RemoteChainProperty";

export class MainView extends CanvasView<ViewCollection> {
    /// Base Colors
    public bg_color = new ChainProperty<this, RgbColor>(this, tools.commons.Colors.BLACK);
    public fg_color = new ChainProperty<this, RgbColor>(this, tools.commons.Colors.WHITE);
    public hi_color = new ChainProperty<this, RgbColor>(this, tools.commons.Colors.RED);
    /// memory cards
    public menu_groups: RemoteChainProperty<this, MenuView, "menu_groups">;
    public camera = new ChainProperty<this, CameraModel | null>(this, null);

    constructor(canvas: HTMLCanvasElement, collection: ViewCollection, menu_view: MenuView) {
        super(canvas, collection);
        this.menu_groups = new RemoteChainProperty<this, MenuView, "menu_groups">(this, this.collection.menu, 'menu_groups')
    }

    public draw(): void {
        if (!this.camera.get()) return;


        this.reset_canvas_state();
        this.collection.menu
            .camera.set(this.camera.get())
            .draw()

        this.context.fillStyle = "#ff0000";
        this.context.fillText("Test", 400, 300);
    }

    /**
     * Reset default canvas state and paint the background
     */
    protected reset_canvas_state() {
        super.reset_canvas_state();
        this.context.resetTransform();
        this.context.fillStyle = this.bg_color.get().to_hex();
        this.context.fillRect(0, 0, 800, 600);
        this.context.fillStyle = this.fg_color.get().to_hex();
        this.context.lineWidth = 2;
        this.context.font = "16px monospace";
        this.context.lineJoin = "round";
        this.context.textAlign = "left";
        this.context.textBaseline = "top";
        this.context.imageSmoothingEnabled = false;
    }
}