import { tools } from "@game.object/ts-game-toolbox/index";
import { RgbColor } from "@game.object/ts-game-toolbox/src/data/RgbColor";
import { ChainProperty } from "@game.object/ts-game-toolbox/src/signals/ChainProperty";
import { ViewCollection } from "../ViewCollection";
import { Rect, RectI } from "@game.object/ts-game-toolbox/src/geometries/Rect";
import { CanvasView } from "@game.object/ts-game-toolbox/src/abstract/mvc/views/CanvasView";
import { CameraModel } from "../../models/CameraModel";

interface ViewMenuButtonAttr {
    background: RgbColor;
    foreground: RgbColor;
    is_visible: boolean;
    title: string;
    collider: RectI;
}

interface ViewMenuGroupAttr {
    background: RgbColor;
    foreground: RgbColor;
    is_visible: boolean;
    collider: RectI;
    title: string;

    children: Array<ViewMenuButtonAttr | ViewMenuGroupAttr>;
}

export class MenuView extends CanvasView<ViewCollection> {
    /// Base Colors
    public bg_color = new ChainProperty<this, RgbColor>(this, tools.commons.Colors.BLACK);
    public fg_color = new ChainProperty<this, RgbColor>(this, tools.commons.Colors.WHITE);
    public hi_color = new ChainProperty<this, RgbColor>(this, tools.commons.Colors.RED);
    /// memory cards
    public menu_groups = new ChainProperty<this, Array<ViewMenuGroupAttr>>(this, []);
    public camera = new ChainProperty<this, CameraModel | null>(this, null);

    public draw(): void {
        if (!this.camera.get()) return;

        this.reset_canvas_state();

        this.context.strokeStyle = "#282828";

        this.menu_groups.get().forEach((group: ViewMenuGroupAttr) => {
            this.draw_menu_group(group);
        });

    }

    protected draw_menu_group(group: ViewMenuGroupAttr) {
        if (!group.is_visible) return;
        this.draw_menu_item_borders(group);
        this.context.save();
        this.context.translate(group.collider.x, group.collider.y);
        this.context.beginPath();
        group.children.forEach((item) => {
            if ('children' in item) {
                this.draw_menu_group(item);
            } else {
                this.draw_menu_button(item);
            }
        });
        this.context.strokeStyle = this.fg_color.get().to_hex();
        this.context.stroke();
        this.context.restore();
    }

    protected draw_menu_button(button: ViewMenuButtonAttr) {
        this.draw_menu_item_borders(button);
        this.context.fillStyle = button.foreground.to_hex();
        this.context.fillText(button.title,
            button.collider.x + button.collider.w / 2,
            button.collider.y + button.collider.h / 2,
        );
    }

    protected draw_menu_item_borders(item: ViewMenuGroupAttr | ViewMenuButtonAttr) {
        if (!item.is_visible) return;
        this.context.fillStyle = item.background.to_hex();
        this.context.fillRect(item.collider.x, item.collider.y, item.collider.w, item.collider.h);
        this.context.rect(item.collider.x, item.collider.y, item.collider.w, item.collider.h);
    }

    /**
     * Reset default canvas state and paint the background
     */
    protected reset_canvas_state() {
        super.reset_canvas_state();
        this.context.resetTransform();
        this.context.lineWidth = 2;
        this.context.font = "16px monospace";
        this.context.lineJoin = "round";
        this.context.textAlign = "center";
        this.context.textBaseline = "middle";
        this.context.imageSmoothingEnabled = false;
    }
}