import { ModelTable } from "@game.object/ts-game-toolbox/src/abstract/mvc/models/ModelTable";
import { ModelCollectionBase } from "@game.object/ts-game-toolbox/src/abstract/mvc/Collections";
import { GameModel } from "./GameModel";
import { MenuButtonModel } from "./MenuButtonModel";
import { CameraModel } from "./CameraModel";
import { MenuGroupModel } from "./MenuGroupModel";

export interface ModelCollection extends ModelCollectionBase {
    game: GameModel,
    camera: CameraModel,
    menu_buttons: ModelTable<ModelCollection, MenuButtonModel>,
    menu_groups: ModelTable<ModelCollection, MenuGroupModel>,
}

export function create_models(): ModelCollection {
    const collection: ModelCollection = {} as ModelCollection;
    return Object.assign(collection, {
        game: new GameModel(collection),
        camera: new CameraModel(collection),
        menu_buttons: new ModelTable(collection, MenuButtonModel),
        menu_groups: new ModelTable(collection, MenuGroupModel),
    });
}