import { ViewCollectionBase } from "@game.object/ts-game-toolbox/src/abstract/mvc/Collections";
import { MainView } from "./main/MainView";
import { MenuView } from "./main/MenuView";
import { TestView } from "./main/TestView";

export interface ViewCollection extends ViewCollectionBase {
    main: MainView,
    menu: MenuView,
    test: TestView,
    partials: {
    }
}

export function create_views(canvas: HTMLCanvasElement): ViewCollection {
    const collection: ViewCollection = {
        partials: {}
    } as ViewCollection;
    collection.menu = new MenuView(canvas, collection);
    collection.test = new TestView(canvas, collection);
    collection.main = new MainView(canvas, collection, collection.menu);
    return collection;
}