import { BaseController } from "../BaseController";
import { EventControllerInterface } from "@game.object/ts-game-toolbox/src/abstract/mvc/controllers/Controller";
import { ControllerRouteResponse, ControllerRouteResponseType } from "@game.object/ts-game-toolbox/src/abstract/mvc/controllers/ControllerRouteResponse";
import { MenuButtonModel } from "../../models/MenuButtonModel";
import { PromisableControllerRouteResponseType, PromiseController } from "@game.object/ts-game-toolbox/src/abstract/mvc/controllers/PromiseController";
import { UserInterfaceEvent } from "../../events/UserInterfaceEvent";
import { UserInterfaceModelAdapter } from "../../models/model-adapters/UserInterfaceModelAdapter";
import { Vector2 } from "@game.object/ts-game-toolbox/src/geometries/Vector2";
import { ControllerEvent, is_controller_event, is_specific_controller_event } from "@game.object/ts-game-toolbox/src/abstract/mvc/events/ControllerEvent";
import { UserInterfaceAction } from "../../config/UserInterfaceAction";

type CustomEventHandler = Map<
    UserInterfaceAction,
    (event: UserInterfaceEvent<UserInterfaceAction>) => ControllerRouteResponse
>;

export class GameEventController extends BaseController implements EventControllerInterface {

    protected custom_event_handler: CustomEventHandler = new Map([
        [
            UserInterfaceAction.TOGGLE_MENU_GROUP,
            (event: UserInterfaceEvent<UserInterfaceAction>) =>
                this.controllers.for_event.menu.toggle_menu_group(event)
        ]
    ]);

    public update(delta_seconds: number): ControllerRouteResponse {
        return null;
    }

    public mouse_pressed(event: MouseEvent, x: number, y: number): ControllerRouteResponse {
        let response: ControllerRouteResponse = null;
        if (!response) response = this.controllers.for_event.menu.mouse_pressed(event, x, y);
        return response;
    }

    public custom_event(event: ControllerEvent): ControllerRouteResponse {
        if (is_specific_controller_event<UserInterfaceEvent<UserInterfaceAction>>(event, "user-interface-event")) {
            const handler = this.custom_event_handler.get(event.action_id);
            if (!handler) {
                console.log("Unhandled Menu Action ", event);
                return null;
            };
            return handler(event);
        }
        return null;
    }

}