import {
    ControllerRouteResponse,
    ControllerRouteResponseType
} from "@game.object/ts-game-toolbox/src/abstract/mvc/controllers/ControllerRouteResponse";
import { RgbColor } from "@game.object/ts-game-toolbox/src/data/RgbColor";
import { UserInterfaceAction } from "../config/UserInterfaceAction";
import { MenuButtonModel } from "../models/MenuButtonModel";
import { MenuGroupModel } from "../models/MenuGroupModel";
import { ModelCollection } from "../models/ModelCollection";
import { MenuBuilder } from "../services/MenuBuilder";
import { BaseController } from "./BaseController";

export class GameController extends BaseController {

    /**
     * Start a new game
     */
    public new_game(): ControllerRouteResponse {

        this.models.camera.center.set(0, 0);
        // How i want it to be
        let menu_builder = new MenuBuilder<UserInterfaceAction, ModelCollection>(
            this.models.menu_groups,
            this.models.menu_buttons,
            UserInterfaceAction.TOGGLE_MENU_GROUP
        );
        menu_builder.set_config({
            button_width: 100,
            direction: "collumn",
        });
        let main_menu_group = menu_builder.from_object(
            "Menu", {
            options: {
                position: { x: 25, y: 15 },
                direction: "row"
            },
            items: {
                "Aktion 1": UserInterfaceAction.OPTION_1,
                "Aktion 2": UserInterfaceAction.OPTION_2,
                "Aktion 3": UserInterfaceAction.OPTION_3,
                "Aktion 4": UserInterfaceAction.OPTION_4,
                "Sub Menu": {
                    items: {
                        "Aktion 1": UserInterfaceAction.OPTION_1,
                        "Aktion 2": UserInterfaceAction.OPTION_2,
                    }
                }
            },
        });
        main_menu_group.is_visible = true;


        /// response
        const response: ControllerRouteResponseType = {
            view: this.views.main
                .menu_groups.set(
                    this.models.menu_groups.all()
                )
                .camera.set(this.models.camera),
            controller: this.controllers.for_event.game_controller,
        };
        return response;
    }

}