import { UserInterfaceAdaptable, UserInterfaceModelAdapter } from "@game.object/ts-game-toolbox/src/abstract/mvc/adapters/UserInterfaceModelAdapter";
import { EventControllerInterface } from "@game.object/ts-game-toolbox/src/abstract/mvc/controllers/Controller";
import { ControllerRouteResponse } from "@game.object/ts-game-toolbox/src/abstract/mvc/controllers/ControllerRouteResponse";
import { ControllerEvent, is_specific_controller_event } from "@game.object/ts-game-toolbox/src/abstract/mvc/events/ControllerEvent";
import { Vector2, Vector2I } from "@game.object/ts-game-toolbox/src/geometries/Vector2";
import { UserInterfaceAction } from "../../config/UserInterfaceAction";
import { UserInterfaceEvent } from "../../events/UserInterfaceEvent";
import { MenuButtonModel } from "../../models/MenuButtonModel";
import { MenuGroupModel } from "../../models/MenuGroupModel";
import { BaseController } from "../BaseController";

export class MenuEventController extends BaseController implements EventControllerInterface {

    public mouse_pressed(event: MouseEvent, x: number, y: number): ControllerRouteResponse {
        const menu_item_clicked = this.get_menu_item_hit(
            this.models.menu_groups.all().sort((a, b) => b.z_index - a.z_index),
            { x, y }
        );
        if (menu_item_clicked) {
            return this.handle_menu_item_selected(menu_item_clicked);
        }
        return null;
    }

    public handle_menu_item_selected(item: MenuButtonModel | MenuGroupModel): UserInterfaceEvent<UserInterfaceAction> | null {
        const action = item.click();
        if (action === null) return null;
        return {
            event_name: "user-interface-event",
            action_id: action,
            target: item,
        };
    }

    public get_menu_item_hit(items: Array<MenuButtonModel | MenuGroupModel>, target: Vector2I): MenuButtonModel | MenuGroupModel | null {
        const hit = items.filter((button: UserInterfaceAdaptable) => {
            return UserInterfaceModelAdapter.for(button).is_clicked(target);
        }).shift() ?? null;
        if (hit instanceof MenuGroupModel && hit.is_visible) {
            const inner_position = new Vector2(target).sub({ x: hit.collider.x, y: hit.collider.y })
            const inner_hit = this.get_menu_item_hit(hit.children, inner_position);
            if (inner_hit) return inner_hit;
        }
        return hit;
    }

    public toggle_menu_group(event: UserInterfaceEvent<UserInterfaceAction>): ControllerRouteResponse {
        const button = event.target;
        if (!(button instanceof MenuButtonModel)) return null;
        const toggled_groups = this.models.menu_groups.all().filter((group) => group.toggle_button_id === button.id);
        toggled_groups.forEach((group: MenuGroupModel) => {
            group.is_visible = !group.is_visible;
        });
        return null;
    }
}