import { tools } from "@game.object/ts-game-toolbox/index";
import { Model } from "@game.object/ts-game-toolbox/src/abstract/mvc/models/Model";
import { Rect } from "@game.object/ts-game-toolbox/src/geometries/Rect";
import { RgbColor } from "@game.object/ts-game-toolbox/src/data/RgbColor";
import { ModelCollection } from "./ModelCollection";
import { UserInterfaceAdaptable } from "./model-adapters/UserInterfaceModelAdapter";
import { UserInterfaceAction } from "../config/UserInterfaceAction";
import { MenuButtonModel } from "./MenuButtonModel";

export class MenuGroupModel extends Model<ModelCollection> implements UserInterfaceAdaptable<UserInterfaceAction> {
    // graphics
    public background: RgbColor = tools.commons.Colors.BLUE;
    public foreground: RgbColor = tools.commons.Colors.WHITE;
    public is_visible: boolean = true;
    public title: string = "Menu Group";
    // logic
    public static next_id = 1;
    public id: number = MenuGroupModel.next_id++;
    public z_index: number = 0;
    public toggle_button_id: number | null = null;
    public collider: Rect = new Rect(0, 0, 0, 0);

    public get is_clickable(): boolean {
        return this.is_visible;
    }

    public click(): UserInterfaceAction | null {
        return null;
    }

    public get children(): Array<MenuButtonModel> {
        return this.models.menu_buttons.all().filter((button: MenuButtonModel) => {
            return button.parent_group_id === this.id;
        });
    }
}